* {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 16px;
}

body {
  background-image: url("hero.b15645d3.jpg");
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

header {
  text-align: center;
  margin-bottom: 30px;
}

.header-quote {
  color: #f6f1ea;
  place-content: center;
  align-items: center;
  height: 8rem;
  padding-top: 30px;
  font-size: 3rem;
  font-weight: bold;
  display: flex;
}

main {
  flex-direction: column;
  place-content: center;
  align-items: center;
  margin-top: 0;
  display: flex;
}

.user-in {
  text-align: center;
  backdrop-filter: blur(500px);
  background-color: #f6f1ea;
  border-radius: 30px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 320px;
  height: 380px;
  margin-top: 50px;
  display: flex;
  box-shadow: 0 50px 50px #0000004d;
}

.user-in h1 {
  color: #585858;
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 1.4rem;
  font-weight: 750;
}

.user-op {
  color: #f6f1ea;
  background-color: #585858;
  border: 4px solid #0000;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 50px;
  margin: 1rem 0;
  font-size: 1rem;
  font-weight: 700;
  display: flex;
}

.user-op:hover {
  color: #585858;
  cursor: pointer;
  background-color: #73c09b99;
  border: 4px solid #585858;
}

.note {
  color: #585858;
  background-color: #f6f1ea;
  border: 4px solid #0000;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  width: 28%;
  height: 15px;
  margin: 5rem .5rem;
  padding: 1rem;
  font-size: .7rem;
  display: flex;
}

.page {
  background-color: #f5f5f5;
  width: 794px;
  height: 1200px;
}

.profile-picture {
  border-radius: 50%;
  width: 150px;
  margin-top: 10px;
}

.personal-info h1 {
  margin-top: 10px;
  font-size: 24px;
}

section {
  background-color: #fff;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: 0 2px 5px #0000001a;
}

h2 {
  border-bottom: 2px solid #ddd;
  margin-bottom: 10px;
  font-size: 20px;
}

.controls {
  text-align: center;
  margin-top: 20px;
}

.page2 {
  background-color: #f5f5f5;
  width: 794px;
  height: 100%;
}

button {
  cursor: pointer;
  color: #f6f1ea;
  background-color: #585858;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  font-size: 16px;
}

@media only screen and (width <= 768px) {
  .personal-info h1 {
    font-size: 20px;
  }

  section {
    padding: 10px;
  }

  button {
    padding: 8px 12px;
    font-size: 14px;
  }
}

form {
  margin: 1rem;
}

fieldset {
  flex-direction: column;
  margin-bottom: 15px;
  display: flex;
}

legend {
  font-weight: bold;
}

button:hover {
  color: #585858;
  background-color: #73c09b99;
}

h1 {
  text-align: center;
  font-size: 2rem;
}

legend {
  text-align: center;
  font-size: 1.25rem;
}

.education-entry, .skill-entry, .work-entry {
  flex-direction: column;
  width: 100%;
  display: flex;
}

#resume-container {
  background-color: #f5f5f5;
  width: 52%;
  height: 100%;
  margin: 2rem 0;
  padding: 1rem 0 1rem 1rem;
}
